import {
	MenuItem,
	Select,
	Typography,
	Grid,
	makeStyles
} from '@material-ui/core';
import { useFormikContext } from 'formik';
import _get from 'lodash.get';
import {
	TJurisdictionalCost,
	TJurisdictionalCostTable,
} from '../../../../../types';
import { isYes } from '../../../../../helpers';
import HoaTable from './HoaTable';
import config from '../../../../../config';

const useStyles = makeStyles({
	hoa: {
		paddingLeft: '0px !important'
	}
})

type Props = {
	jurisdictionalCost: TJurisdictionalCost;
	isDisabled?: boolean;
};

export const Hoa = ({ jurisdictionalCost, isDisabled }: Props) => {
	const classes = useStyles();
	const { values, handleChange } = useFormikContext<TJurisdictionalCostTable>();
	const isHoaDetected = Boolean(_get(jurisdictionalCost, 'HOADetected', false));

	const isDisplay = isYes(values.hoaDetectedOverride) || false;

	return (
		<>
			<Grid container spacing={2} m={0}>
				<Grid item xs={9}>
					<Typography variant='body2'>HOA Fees</Typography>
				</Grid>
				<Grid item xs={3} className={classes.hoa}>
					<Typography variant='body2'>{isHoaDetected ? 'Yes' : 'No'}</Typography>
				</Grid>
				<Grid item xs={9}>
					<Typography variant='body2'>HOA Fees Override</Typography>
				</Grid>
				<Grid item xs={3} className={classes.hoa}>
					<Typography variant='body2'>
						<Select
							labelId='hoaDetectedOverride'
							id='hoaDetectedOverride'
							value={values.hoaDetectedOverride}
							label='hoaDetectedOverride'
							onChange={handleChange}
							name='hoaDetectedOverride'
							displayEmpty
							disabled={isDisabled}
						>
							<MenuItem value=''>{config.defaultText.noneValue}</MenuItem>
							<MenuItem value='No'>No</MenuItem>
							<MenuItem value='Yes'>Yes</MenuItem>
						</Select>
					</Typography>
				</Grid>
			</Grid>
			{isDisplay && (
				<HoaTable jurisdictionalCost={jurisdictionalCost} isDisabled />
			)}
		</>
	);
};

export default Hoa;
